<template>
  <section class="demo">

    <dl class="list maki">
      <dt>GIF THEMES</dt>
      <dd><router-link to="/sticker">Stickers GIFs</router-link></dd>
      <dd><router-link to="/trending">Trending GIFs</router-link></dd>
      <dd><router-link to="/random">Random GIFs</router-link></dd>
